import PokemonAPIService from '../../shared/api/service/PokenmonAPIService'
import {useState,useEffect} from 'react'
import { useHistory } from 'react-router-dom'
import RoutingPath from '../../routes/RoutingPath'
 

export const NewsView = () => {
    const [APIdata ,setAPIData ] = useState([])
    const [loadning,setLoadning]= useState(true)
    const history = useHistory()

    const fetchData = async () => {
        try{
            const {data} = await PokemonAPIService.getPokemonAPI()
            setAPIData(data)
            setLoadning(false)
        } catch (error) {
            console.erro(error)
        }   
    }
    
    const displayData = () => {
        if(!loadning) {
            return APIdata.results?.map ((x) =>
                <div key={x.name} >
                    <h1 onClick={() => history.push(RoutingPath.detailsView, x)}>{x.name}</h1>
                </div>  
            )  
    
        }
    }

    useEffect(() => 
    {  fetchData()

    }, [])

    return (
        <div>
            {displayData()}
        </div>
    )
}
