import {useLocation} from 'react-router-dom'
import {useEffect,useState} from 'react'
import Axios from 'axios'
import loading from '../shared/images/loading.gif'



export const DetailView = () => {
    const location = useLocation()
    const [APIdata , setAPIData] = useState()
    const[loading,setLoading] = useState(true)

    const fetchData = async() => {
        try{
            const {data} = await Axios.get (location.state.url)
            setAPIData(data)
            setLoading(false)
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        fetchData()
    }, [])

    const displaydata = () => {
        if(!loading)  { 
            return(
            <>   
                <img src = {APIdata?.sprites?.back_default} alt='error'/>
                <h1>name :{APIdata?.name}</h1>
                <h1>hair_color :{APIdata?.hair_color}</h1>
                <h1>gender :{APIdata?.gender}</h1>
                <h1>height :{APIdata?.height}</h1>
            </>    
            )
        }  
    }

    return (
        loading ? <img src={loading}/>:
        <div>
            <h1>{location.state.name}</h1>
            <button onClick={() => console.log(APIdata) }> show location </button>
            {displaydata()}
        </div>
    )
}
