import http from '../PokemonAPI'

const getCharaterDitto = () => {
    return  http.get ('pokemon/ditto')
}

const serchCharacter = (pokemonName: string ) => {
    return http.get (`/pokemon/${pokemonName}`)

}

const getPokemonAPI =() => {
    return http.get(`pokemon?limit=100}`)
}
export default {
    getCharaterDitto,
    serchCharacter,
    getPokemonAPI
}
