//import { useEffect, useState } from 'react'
import PokemonAPIService from '../../shared/api/service/PokenmonAPIService'
import {useEffect , useState} from 'react'
import {useDebounce} from '../../hooks/useDebounce'


export const ShopView = () => {
	const [data , setData] = useState()
	const [search, setSearch] = useState('')
	const debounceSerchTerm = useDebounce(search, 2000)

	const fetchdata = async (x: String) => {
		if (debounceSerchTerm) {
		const { data } = await PokemonAPIService.serchCharacter(x)
		setData(data)
		}
	}

	useEffect (() => {
		fetchdata(search)
	} , [debounceSerchTerm])

	return (
		<div>
			<button onClick={() => fetchdata()}>get data</button>
			<button onClick={() => console.log(data)}>show data</button>
			<input placeholder='serach for pokemon' onChange={event => setSearch(event.target.value)}/>
			<h1>name :{data?.name}</h1>
			<h1>height :{data?.height}</h1>
			<h1>id :{data?.id}</h1>
			<h1>weight :{data?.weight}</h1>
			<h1>type :{data?.types?.[0]?.type.name}</h1>
		</div>
	)
	
	/*const [counter, setCounter] = useState(1)

    useEffect(() => {
        alert('Firsr alert')
        return () => {
            alert('last render!')
        }
    }, [counter])

	return (
		<div>
			<h1 onClick={() => setCounter(counter + 2)}>{counter}</h1>
			<h1>This is the shopview</h1>
		</div>
	)*/
}