import React,{useContext, useEffect} from 'react'
import {UserContext} from '../shared/provider/UserProvider'
import {BrowserRouter , Switch, Route} from 'react-router-dom'
import {HomeView} from '../view/HomeView'
import {GalleryView} from '../view/navigationtabviews/GalleryView'
import {NewsView} from '../view/navigationtabviews/NewsView'
import {ShopView} from '../view/navigationtabviews/ShopView'
import {SignInView} from '../view/navigationtabviews/SignInView'
import RoutingPath from './RoutingPath'
import {ProfileView} from '../view/profildropdownviews/ProfileView'
import {SavedProductsView} from '../view/profildropdownviews/SavedProductsView'
import {SettingsView} from '../view/profildropdownviews/SettingsView'
import {DetailView} from '../view/DetailView'


    export const Routes = ({children}) => {  
        
        const [authenticatedUser, setAuthenticatedUser] = useContext(UserContext)

        const blockRouteIfAut = (navigateToViewAuthenticated :React.FC) => {
            return authenticatedUser ? HomeView : navigateToViewAuthenticated
        }    

        const autRequired = (navigateToViewAuthenticated :React.FC) => {
            return authenticatedUser ? navigateToViewAuthenticated : SignInView
        }  

            /*if (authenticatedUser) {
                return HomeView
            } else{
                return navigateToViewAuthenticated
                
            }*/


        const chekIfUserAut = () => {
            const getLocalStorage = localStorage.getItem('username')
            if (getLocalStorage) {
                setAuthenticatedUser(getLocalStorage)
            }
        }
    
        useEffect(() => {
            chekIfUserAut()
        })
   
        return (
            <BrowserRouter>
            {children}
                <Switch>
                    <Route exact path={RoutingPath.galleryView} component ={GalleryView} />
                    <Route exact path={RoutingPath.newsView} component ={NewsView} />
                    <Route exact path={RoutingPath.shopView} component ={ShopView} />
                    <Route exact path={RoutingPath.signInView} component ={SignInView} />

                    <Route exact path={RoutingPath.savedProductsView} component ={SavedProductsView} />
                    <Route exact path={RoutingPath.profileView} component ={ProfileView} />
                    <Route exact path={RoutingPath.detailsView} component ={DetailView} />
                    <Route component={HomeView} />
                </Switch>
            </BrowserRouter>
        )
    }